.gray {
  color: #FFFFFF !important;
}
.auction-page .title {
  margin-top: 0.9375rem;
  margin-left: 0.9375rem;
  margin-bottom: 0.9375rem;
  font-size: 1.125rem;
  font-weight: 400;
  color: #ffffff;
}
.auction-page .auction-list {
  padding: 0.9375rem;
  overflow-y: auto;
}
.auction-page .auction-list .list-item {
  height: 8.125rem;
  margin-bottom: 0.9375rem;
  width: 100%;
  border: 2px solid #666666;
  background: linear-gradient(-30deg, #333333, #111111);
}
.auction-page .auction-list .list-item .top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  height: 2.5625rem;
  position: relative;
}
.auction-page .auction-list .list-item .top:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #666666;
  color: #666666;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.auction-page .auction-list .list-item .top .left {
  display: flex;
  justify-content: center;
}
.auction-page .auction-list .list-item .top .left .left {
  min-width: 4.0625rem;
  height: 1.15625rem;
  line-height: 1.15625rem;
  border-radius: 0.1875rem;
  font-size: 0.625rem;
  margin-right: 0.3125rem;
  color: #ffffff;
}
.auction-page .auction-list .list-item .top .left .bidding {
  background: #16ba7e;
}
.auction-page .auction-list .list-item .top .left .begin {
  background: #fd982f;
}
.auction-page .auction-list .list-item .top .left .end {
  background: linear-gradient(90deg, #555555, #999999);
}
.auction-page .auction-list .list-item .top .left .right {
  font-size: 0.75rem;
  font-weight: 400;
  color: #ffffff;
}
.auction-page .auction-list .list-item .top .right {
  font-size: 0.625rem;
  font-weight: 400;
  color: #ffffff;
  height: 1.15625rem;
  line-height: 1.15625rem;
}
.auction-page .auction-list .list-item .top .red {
  color: #ff3333 !important;
}
.auction-page .auction-list .list-item .center {
  display: flex;
  flex: 4 1;
  align-items: center;
  color: #ffffff;
  height: 3.125rem;
  padding-left: 0.9375rem;
  padding-top: 0.3125rem;
  padding-bottom: 0.3125rem;
}
.auction-page .auction-list .list-item .center .item:nth-child(3) p {
  margin-left: 1.875rem;
}
.auction-page .auction-list .list-item .center .item {
  flex: 1 1;
}
.auction-page .auction-list .list-item .center .item p:nth-child(1) {
  font-size: 1.1875rem;
  font-weight: 600;
  color: #00d2ff;
}
.auction-page .auction-list .list-item .center .item p:nth-child(1) .little {
  font-size: 0.6875rem;
}
.auction-page .auction-list .list-item .center .item p:nth-child(2) {
  font-size: 0.6875rem;
  font-weight: 400;
  color: #999999;
}
.auction-page .auction-list .list-item .bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-size: 0.75rem;
  height: 1.875rem;
  position: relative;
}
.auction-page .auction-list .list-item .bottom:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #666666;
  color: #666666;
  transform-origin: 0 0;
  transform: scaleY(0.5);
}
.auction-page .auction-list .list-item .blue {
  color: #00d2ff !important;
}
.offer-price-modal .title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 2.5rem;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  position: relative;
}
.offer-price-modal .title:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #666666;
  color: #666666;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.offer-price-modal .title .right {
  font-size: 0.875rem;
  color: #ffffff;
  font-weight: 400;
}
.offer-price-modal .title .left {
  font-size: 0.5625rem;
  color: #ffffff;
  font-weight: 400;
}
.offer-price-modal .price-detail {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.6875rem;
  height: 1.875rem;
  color: #999999;
}
.offer-price-modal .price-detail .red {
  color: #FF3333;
}
.offer-price-modal .offer-price-input {
  margin-top: 0.8125rem;
  background: rgba(255, 255, 255, 0);
}
.offer-price-modal .offer-price-input .am-list-line {
  position: relative;
}
.offer-price-modal .offer-price-input .am-list-line:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #ffffff;
  color: #ffffff;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.offer-price-modal .offer-price-input .password .am-input-extra {
  color: #00D2FF !important;
}
.offer-price-modal .offer-price-input .password .am-list-line {
  position: relative;
}
.offer-price-modal .offer-price-input .password .am-list-line:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #ddd;
  color: #ddd;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.offer-price-modal .offer-price-input .am-list-item {
  background: rgba(255, 255, 255, 0);
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
}
.offer-price-modal .offer-price-input .am-list-item .am-list-line {
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}
.offer-price-modal .offer-price-input .am-list-item .am-list-line input {
  font-size: 0.75rem;
  font-weight: 400;
  color: #666666;
}
.offer-price-modal .offer-price-input .am-list-item .am-list-line .am-input-extra {
  color: #ffffff;
}
.offer-price-modal .des-tip {
  display: flex;
  justify-content: flex-end;
  padding-top: 0.625rem;
  padding-right: 1.5625rem;
  font-size: 0.5625rem;
  color: #00D2FF;
}
.offer-price-modal .des-tip .red {
  color: #FF3333 !important;
}
.offer-price-modal .footerButton {
  margin-top: 2.5rem;
  margin-bottom: 3.125rem;
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
}
